<template>
  <div>

<nav-bar :name="title"></nav-bar>

        <v-container class="text-center">
        <router-link to="/addsale">
        <v-btn color="#E47911" large height="40px" dark v-if="isStaff">
          <v-icon
            left
            dark
            > mdi-plus 
          </v-icon>  Add a sale register
        </v-btn>
        </router-link>
        </v-container>
      <v-container class="text-center">
      <v-card class="ma-auto" width="90%" outlined>
        <br>
        <v-card class="ma-auto" width="75%" tile>
          <p class="font-weight-bold text-left px-3 py-1">General sales</p>
          <v-divider></v-divider>
            <v-expansion-panels focusable>
              <v-expansion-panel
                v-for="(item,id) in sales"
                :key="id"
              >
                <v-expansion-panel-header class="font-weight-light text-left px-2">
                  {{ item.name }}
                </v-expansion-panel-header>
                  <v-expansion-panel-content v-if="item.name!=='Current sales'"> 
                  <v-simple-table>
                    <template v-slot:default>
                      <tbody>
                        <tr>
                          <td>{{ item.total }} </td>
                          <td>$ {{ item.amount }} </td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                   </v-expansion-panel-content>
                <v-expansion-panel-content v-else>
                <v-simple-table>
                  <template v-slot:default>
                    <tbody>
                      <tr>
                        <td>{{ item.total_day }} </td>
                        <td>$ {{ item.amount_day }} </td>
                      </tr>
                      <tr>
                        <td>{{ item.total }} </td>
                        <td>$ {{ item.amount }} </td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
        </v-card>

        <br>

        <v-card class="ma-auto" width="75%" tile v-if="isStaff">
          <p class="font-weight-bold text-left px-3 py-1">Total sold by store</p>
          <v-divider></v-divider>
            <v-expansion-panels focusable>
              <v-expansion-panel
                v-for="(item,id) in stores"
                :key="id"
                @click="loadDataSales(id)"
              >
              <v-expansion-panel-header class="font-weight-light text-left px-2">
                  {{ item.name1 }}
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <v-card>
                    <v-card-title>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :search="search"
                      :items="items"
                      :loading="loading"
                      loading-text="Loading... Please wait"
                      hide-default-footer
                      :page.sync="page"
                      :items-per-page="itemsPerPage"
                      @page-count="pageCount = $event"
                      >
                      <template v-slot:item.unique_id="{ item }">
                        <router-link
                          :to="getLink(item.unique_id)"
                        >
                          View <v-icon color="#E47911"> mdi-eye </v-icon>
                        </router-link>
                      </template>
                    </v-data-table>
                    <v-pagination
                      v-model="page"
                      :length="pageCount"
                    ></v-pagination>
                  </v-card>
              <br>
                <p class="font-weight-bold text-center py-2">Total sales: ${{ item.total1 }}</p>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
        </v-card>
        <br>
        <p class="font-weight-bold text-center py-2">Total sales: ${{ amount_total }}</p>
      </v-card>
      </v-container>
<footer-nav></footer-nav>
  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import SalesService from '@/services/SalesService.js';

  export default {
    components: {
      NavBar, FooterNav
    },
    data: () => ({
      title: 'SALES',
      amount_total: 0,
      load: false,
      loading: true,
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      sales: [
        {
          name: "Current sales",
          total_day: "Total Current Day Sales",
          amount_day: 0,
          total: "Total Current Month Sales",
          amount: 0
        },
        {
          name:"Last month sales",
          total: "Total Last Month Sales",
          amount: 0
        },
        {
          name: "Past 3 month sales",
          total: "Total Past 3 Months Sales",
          amount: 0
        },
      ],
      stores: [
        {
          method: "loadCurrent()",
          name1: "Current month",
          total1: 0
        },
        {
          method: "loadCurrent()",
          name1: "Last month",
          total1: 0
        },
        {
          method: "loadCurrent()",
          name1: "Past 3 months",
          total1: 0
        },
      ],
      items: [
        
      ],
      search: '',
      headers: [
        {
          text: 'Store',
          value: 'name'
        },
        { 
          text: 'Total sold',
          value: 'total_sold'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
    }),
    
    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },

      idStore(){
        if(!this.isStaff)
          return this.$store.state.auth.store_user.unique_id;
      },
    },

    mounted() {
      if(!this.isStaff) {
        SalesService
          .general_store(this.idStore)
          .then(res => {
            this.sales[0].amount_day = parseFloat(res.total_current_day_sales).toFixed(2);
            this.sales[0].amount = parseFloat(res.total_current_sales).toFixed(2);

            this.sales[1].amount = parseFloat(res.total_last_month_sales).toFixed(2);

            this.sales[2].amount = parseFloat(res.total_three_months_before_sales).toFixed(2);

            this.amount_total = parseFloat(res.total_sales).toFixed(2);
          })

      }

      else {
        SalesService
          .general_admin()
          .then(res => {
            this.sales[0].amount_day = parseFloat(res.total_current_day_sales).toFixed(2);
            this.sales[0].amount = parseFloat(res.total_current_sales).toFixed(2);

            this.sales[1].amount = parseFloat(res.total_last_month).toFixed(2)

            this.sales[2].amount = parseFloat(res.total_three_months).toFixed(2)

            this.amount_total = parseFloat(res.total_sales).toFixed(2)
          })
      }
    },

    methods: {
      getLink (unique_id) {
        console.log(unique_id)
        return '/viewstore'
      },

      loadDataSales(id){

        if(id==0){
          this.loading=true;
          SalesService
            .current_month()
            .then(res => {
              this.loading=false;
              this.items = res.current_month
              this.stores[0].total1 = parseFloat(res.total_current_month).toFixed(2)
            });
          
        }

        else if(id==1){
          this.loading=true;
          SalesService
            .last_month()
            .then(res => {
              this.loading=false;
              this.items = res.current_month
              this.stores[1].total1 = parseFloat(res.total_last_month).toFixed(2)
            });
        }

        else if(id==2){
          this.loading=true;
          SalesService
            .three_months()
            .then(res => {
              this.loading=false;
              this.items = res.current_month
              this.stores[2].total1 = parseFloat(res.total_three_months).toFixed(2)
            });
        }
      }
    }
  }
</script>

<style>
.v-expansion-panel-header--active{
  background-color: #E47911;
  color: white;
}
.v-expansion-panel-header--active .theme--light.v-expansion-panels .v-expansion-panel-header .v-expansion-panel-header__icon .v-icon {
  color: white !important;
}
</style>