import http from '@/assets/http-commom.js'
import Swal from 'sweetalert2'

class SalesService {
    general_store(id){
        return http
        .get('sales/'+id+'/general/')
        .then(res => {
            return res.data
        })
        .catch(e => {
            /* 
            *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
            */

            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: e.response.data
            });

        })
    }

    general_admin(){
        return http
            .get('admin/sales/general/')
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    current_month(){
        return http
            .get('admin/sales/current_month/')
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    last_month(){
        return http
            .get('admin/sales/last_month/')
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    three_months(){
        return http
            .get('admin/sales/three_months/')
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    add_sales(data){
        let formData = new FormData();

        formData.append("file", data);
        return http
            .post('admin/sales/',formData,{
            headers: {
                "Content-Type": "multipart/form-data"
              },}
              )
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data['file']
                });
    
            })
            
    }

    delete_stores(data){
        return http
            .post('admin/stores/remove_stores/', {
                id_stores: data
            })
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    export_stores(data){
        return http
            .post('admin/stores/export_stores/', {
                id_stores: data
            })
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    add_walmart(data){
        return http
            .post('walmart/', {
                store: data.store,
                walmart_username: data.walmart_username,
                walmart_password: data.walmart_password,
                last_4_digits_of_the_card: data.card,
                CVV: data.CVV,
                month: data.month,
                year: data.year
            })
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }

    update_walmart(data, pk){
        return http
            .put('walmart/'+pk+'/', {
                store: data.store,
                walmart_username: data.username,
                walmart_password: data.password,
                last_4_digits_of_the_card: data.card,
                CVV: data.CVV,
                month: data.month,
                year: data.year
            })
            .then(res => {
                return res.data
            })
            .catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
    }
}

export default new SalesService();